.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    overflow-y: auto; /* Для прокрутки, если контент превышает высоту экрана */
}

.modal-content {
    background-color: #fff;
    padding: 20px;
    border-radius: 5px;
    width: 90%; /* Измените при необходимости */
    max-width: 500px;
    position: relative;
    box-sizing: border-box;
}
